import React, { useCallback, useEffect, useState, memo } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import Button from '../common/UI/Button/Button';
import { setSelectedProcess, setProcessModule, setShowMenu, resetFlowReducer, setShowSideMenu, setCompanyProcesses, setPayable, setMessageBar, setCurrentLocation, setTasks, setUsersArray } from '../../reduxStore/actions/flowActions';
import cls from './DashboardMobile.module.scss';
import { getProcessById } from '../../services/processes.service';
import { getComponents, getFieldTypes } from '../../services/components.service';
import { APPEND_TO_DATA, computeFormula, getDateForm, dashboard_page_default, APP_NAME, isUserAuthorized, pagination_max, FLOWHERO, BYFLOWHERO, generateNewUUID, page_default, app_max, CLEAR_TO_SET_DATA } from '../../shared/utility';
import { store } from '../../reduxStore/store';
import { first, take } from 'rxjs/operators';
import { getAlComponentsByModuleId } from '../../services/module.service';
import { LogoutModal, ModalR, Spinner } from '../common';
import { useProcessTaskContext, processTaskAction } from '../../context/processTask.context';

import { isMobile } from 'react-device-detect';
import { connect, useDispatch } from 'react-redux';
import moment, { now } from 'moment';
import { resetAuthReducer } from '../../reduxStore/actions/authActions';
import { faBarsStaggered, faCalendar, faHome, faList, faRectangleXmark, faUser, faXmark } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import ProcessModule, { MobileTaskModal } from './ModulesComp/ProcessModule';
import CheckoutPage from './CheckoutPage/CheckoutPage';
import { getPurchases, purchase } from '../../services/payments.service';
import { getAuthenticatedUser, getFilteredUsers, getUsersByPositions, updateUserOptionsById } from '../../services/user.service';
import MyProfile, { LabeledContainer } from '../TopBar/MyProfile/MyProfile';
import { PAYMONGO_KEY } from '../../services/API_CONSTANTS';

import { register } from 'swiper/element/bundle';
import { NotificationsMenuEntry } from '../TopBar/HeaderMenuButtons/HeaderMenuButtons';
import NotificationsProvider from '../../context/notifications.context';
import ModuleLists from './ModulesComp/ModuleLists';
import { add, reset2 } from '../../assets/images/icons';
import { getAllTasks } from '../../services/tasks.service';
import { sessionService } from 'redux-react-session';

import { Autoplay, Pagination, Grid } from 'swiper/modules';
import { Swiper, SwiperSlide } from 'swiper/react';

import 'react-tabs/style/react-tabs.css';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';
import 'swiper/css/grid';
import 'swiper/css/effect-coverflow';
import './DashboardMobile.css';

const R = require('ramda');

const DashboardMobile = props => {
	register();

	const { authenticated, authenticatedUser, user, companyProcesses, allModules, components, companyData, begunModule, blueprints, payable, companyUsersArray, lastUpdates, allTasks } = props;

	const [loading, setLoading] = useState(false);
	const [selectedComponent, setSelectedComponent] = useState({});
	const [userLogout, setUserLogout] = useState(false);
	const [refreshComponent, setRefreshComponent] = useState(false);
	const [showProfile, setShowProfile] = useState(false);
	const [showTasks, setshowTasks] = useState(false);
	const [showTask, setShowTask] = useState();

	const [start, setStart] = useState(false);

	const [navigateTo, setNavigateTo] = useState({});
	const [showModuleNavigator, setShowModuleNavigator] = useState(false);
	const [hasActiveTx, setHasActiveTx] = useState({});
	const [showPayments, setShowPayments] = useState(false);
	const [amount, setAmount] = useState(-1);
	const [invoices, setInvoices] = useState([]);
	const [showHistory, setShowHistory] = useState(false);
	const [nav, setNav] = useState(false);
	const [taskProcessContext, setTaskProcessContext] = useProcessTaskContext();
	const [isProcessorOnly, setIsProcessorOnly] = useState(begunModule?.processModule?.module?.id ? user?.positions?.filter(pos => begunModule?.processModule?.module?.options?.processors?.indexOf(pos.id) > -1)?.length > 0 : false);
	const dispatch = useDispatch();
	const navigate = useNavigate();

	const { paymentID, walletID } = useParams();

	document.title = `Dashboard | ${APP_NAME} Mobile`;

	const setInitialScreen = useCallback(
		(modules, module, isProcessor) => {

			const processData = (data) => {

				const steps_for_use = data.steps
					.filter(step => step?.options?.showStepAll == undefined || step?.options?.showStepAll == true || (step?.options?.showStepAll == false && step?.options?.showOnly?.filter(a => authenticatedUser.positions.map(pos => pos.id).indexOf(a) > -1).length > 0))
					.map((step, i) => {
						return { ...step, rank: i + 1 };
					});

				store.dispatch(setProcessModule(data));
				store.dispatch(setCompanyProcesses([data], APPEND_TO_DATA));
				store.dispatch(
					setProcessModule({
						...data,
						completeSteps: data.steps,
						steps: steps_for_use,
						stepIndex: isProcessor ? 0 : data?.steps?.length > 0 ? (data?.steps[0]?.component_blueprints?.filter(cb => cb.options?.asModuleList == true).length > 0 ? 1 : 0) : 1,
						module_id: module.id,
						module: modules.find(mod => mod.id === module.id),
						isUpdating: false,
					})
				);

				setTaskProcessContext({ action: processTaskAction.setCurrentStep, value: data?.steps[isProcessor ? 0 : data?.steps?.length > 0 ? (data?.steps[0]?.component_blueprints?.filter(cb => cb.options?.asModuleList == true).length > 0 ? 1 : 0) : 1] });

				setTaskProcessContext({
					action: processTaskAction.setModuleItems,
					value: selectedComponent.component_items,
				});

				setTaskProcessContext({
					action: processTaskAction.setModuleNewStepComponents,
					value: [],
				});
				setTaskProcessContext({
					action: processTaskAction.setComponents,
					value: [],
				});

				//clear all values
				begunModule?.processModule?.steps?.length > 0 && begunModule?.processModule?.steps?.filter(step => step?.component_blueprints?.filter(cb => cb.selected_fields.filter(field => (field.value = undefined))));

				getAlComponentsByModuleId(module, APPEND_TO_DATA, dashboard_page_default, 1, false)
					.pipe(take(1))
					.subscribe({
						next: data => {
							setLoading(false);
							module.lastUpdate = 0;
							module.count = data?.total ?? 0;

							getComponents(true, false)
								.pipe(take(1))
								.subscribe({
									complete: () => {
										getFieldTypes().pipe(take(1)).subscribe({});
									},
								});
						},
					});

				module?.options?.supporting?.filter((support, index) => {
					getAlComponentsByModuleId({ id: support, name: `${allModules?.find(mod => mod.id === support)?.name}${index}` ?? `${module?.name}${index}` }, APPEND_TO_DATA, app_max, 1)
						.pipe(take(1))
						.subscribe({
							next: (data) => {
								lastUpdates.componentsByModule[`${allModules?.find(mod => mod.id === support)?.name}${index}` ?? `${module?.name}${index}`] = now();
							},
						})
				});

				if (module?.options?.positions?.length > 0) {
					getUsersByPositions(module?.options?.positions, (module?.options?.positions.length * 5))
						.pipe(take(1))
						.subscribe({});
				}

				if (module?.options?.single) {
					const cmp = localStorage.getItem(module.id);
					goToTransaction(data, cmp ? JSON.parse(cmp) : components.find(co => co.module_id === module.id));
				}
			}

			if (companyProcesses.find(proc => proc.id === module.procedure.id)) {
				processData(companyProcesses.find(proc => proc.id === module.procedure.id));
			} else {
				setLoading(true);
				getProcessById(module.procedure.id)
					.pipe(first())
					.subscribe({
						next: data => {
							setLoading(false);
							processData(data);
						},
						error: _error => { },
					});
			}
		},
		[begunModule]
	);

	useEffect(() => {
		history.pushState(null, null, location.href);
		window.onpopstate = function (event) {
			history.go(1);
		};

		dispatch(setShowSideMenu(false));
		dispatch(setShowMenu(false));
	}, [showProfile]);

	useEffect(() => {
		if (authenticated) {
			// if (!isMobile) {
			// 	navigate('/dashboard');
			// }
			if (!(companyData?.status === 'trial_ended' || companyData?.status === 'inactive')) {
				if (allModules?.length > 0) {
					const modules_found = allModules?.filter(mod => mod?.options?.asMobileApp === true);
					//checks here if what modules are run
					if (modules_found?.length === 1) {
						const ipo = user?.positions?.filter(pos => modules_found[0]?.options?.processors?.indexOf(pos.id) > -1)?.length > 0
						setIsProcessorOnly(ipo);
						setInitialScreen(allModules, modules_found[0], ipo);
					} else if (modules_found?.length > 0) {
						const target_position = authenticatedUser.positions.find(pos => pos.standards === '1');
						const target_module = target_position && modules_found.find(mod => mod.name === target_position.name);
						let ipo = user?.positions?.filter(pos => modules_found[0]?.options?.processors?.indexOf(pos.id) > -1)?.length > 0;
						if (target_module) {
							ipo = user?.positions?.filter(pos => target_module?.options?.processors?.indexOf(pos.id) > -1)?.length > 0;
							setInitialScreen(allModules, target_module, ipo);
						} else {
							setInitialScreen(allModules, modules_found[0], ipo);
						}
						setIsProcessorOnly(ipo);
						setShowModuleNavigator(true);
					}
				}
			}

			if (user?.id === '') {
				dispatch(resetAuthReducer());
				dispatch(resetFlowReducer());
			}

			if ("geolocation" in navigator) {
				navigator.geolocation.getCurrentPosition(function (position) {
					store.dispatch(setCurrentLocation({
						latitude: position.coords.latitude,
						longitude: position.coords.longitude,
					}))
				});
			}

		}
	}, [authenticated, allModules]);

	useEffect(() => {

		allModules.length > 0 &&
			allModules.filter(module => companyProcesses.find(pro => pro.id === module?.procedure?.id) === undefined && getProcessById(module?.procedure?.id)
				.pipe(first())
				.subscribe({
					next: () => window.scrollTo({ top: -10 })
				})
			);
		allModules.length > 0 &&
			allModules.filter(module =>
				(moment(now()).diff(moment(lastUpdates.componentsByModule[module.name] ?? now()), 'minutes') > 10 || lastUpdates.componentsByModule[module.name] === undefined || components.filter(co => co.module_id === module.id).length === 0) &&
				getAlComponentsByModuleId(module, APPEND_TO_DATA, pagination_max, 1, false)
					.pipe(take(1))
					.subscribe({
						next: (cmps) => {
							lastUpdates.componentsByModule[module.name] = now();
							window.scrollTo({ top: -10 })
						},
					})
			);

	}, [allModules]);

	useEffect(() => {
		if (authenticated) {
			if (payable?.length > 0 && walletID !== undefined && paymentID === undefined) {
				payable.map((pay, index) => {
					setLoading(true);
					purchase({
						user_id: pay?.user_id,
						id: pay?.id,
						total: Math.abs(+(pay?.amount ?? 0)),
						product: pay?.product,
						notes: 'E-Payment',
						quantity: 1,
					})
						.pipe(first())
						.subscribe({
							next: () => {
								setLoading(false);
								if (pay.paymentID === walletID) {
									setLoading(true);
									const amount = (user?.options?.credit ?? 0.00) + Math.abs(+pay?.amount ?? 0);
									updateUserOptionsById(user, { ...user?.options, credit: +amount.toFixed(2) })
										.pipe(first())
										.subscribe({
											next: _data => {
												user.options = _data.options;
												setLoading(false);
											},
										});
								}
								if (index + 1 == payable.length) {
									store.dispatch(setPayable([]));
									navigate('/mobile');
								}
							},
						});
				});
			}
		}
	}, [walletID, authenticated]);

	const Tabbar = useCallback(
		props => {

			return (
				<div className="tabbar-section">
					{props.children}

					{/* Hamburger Icon */}

					<div className="mobile-nav">
						<Button

							clear
							loading={loading}
							clicked={() => {
								store.dispatch(
									setProcessModule({
										...begunModule.processModule,
										stepIndex: isProcessorOnly ? 0 : begunModule.processModule?.module?.options?.single ? 0 : 1,
										isUpdating: false,
									})
								);
								if (allModules
									.filter(mod => mod?.options?.asMobileApp === true).length > 1) {
									setShowModuleNavigator(true);

								}
								setShowProfile(false);
								setshowTasks(false);
							}}
						>
							<div style={{ color: 'var(--app-color-gray-dark)' }}>
								<FontAwesomeIcon icon={faHome} className="tabbar-icon" />
								<div style={{ fontSize: '10px', height: '14px' }}>
									{'Home'}
								</div>
							</div>
						</Button>
					</div>

					{user?.ui_permissions?.find(permis => permis === 'view-dashboard-task') && <div className="mobile-nav">
						<Button
							clear
							// loading={loading}
							clicked={() => {
								setshowTasks(true);
								setShowProfile(false);
							}}
						>
							<div style={{ color: 'var(--app-color-gray-dark)' }}>
								<FontAwesomeIcon icon={faCalendar} className="tabbar-icon" />
								<div style={{ fontSize: '10px', height: '14px' }}>
									{'Tasks'}
								</div>
							</div>
						</Button>
					</div>}

					{!begunModule?.processModule?.module?.options?.single && <div className="mobile-nav">
						<Button
							clear
							clicked={() => {
								store.dispatch(
									setProcessModule({
										...begunModule.processModule,
										stepIndex: 0,
										isUpdating: false,
									})
								);
								setRefreshComponent(true);
								setShowModuleNavigator(false);
								setTimeout(() => setRefreshComponent(false));
								setNavigateTo({});
								setStart(true);
								setShowProfile(false);
								setshowTasks(false);
							}}
						>
							<div style={{ color: 'var(--app-color-gray-dark)' }}>
								<FontAwesomeIcon icon={faList} className="tabbar-icon" /><br />
								<div style={{ fontSize: '10px', height: '14px' }}>
									{'Transactions'}
								</div>
							</div>
						</Button>
					</div>}

					<div className="mobile-nav">
						<Button
							clear
							clicked={() => {
								setShowProfile(true);
								setshowTasks(false);
							}}
						>
							<div style={{ color: 'var(--app-color-gray-dark)' }}>
								<FontAwesomeIcon icon={faUser} className="tabbar-icon" />
								<div style={{ fontSize: '10px', height: '14px' }}>
									{'Profile'}
								</div>
							</div>
						</Button>
					</div>
				</div>
			);
		},
		[nav, loading, taskProcessContext, components]
	);

	const Navbar = useCallback(
		props => {
			const openNav = () => {
				setNav(!nav);
			};

			return (
				<div className="navbar-section">
					{props.children}

					{/* Hamburger Icon */}

					<div className="mobile-nav">
						<Button
							clear
							clicked={() => {
								setLoading(false);
								if (taskProcessContext?.components?.length > 0 || taskProcessContext?.newStepComponents?.length > 0) {
									setHasActiveTx({ open: true, handler: openNav });
								} else {
									openNav();
								}

								openNav();
							}}
						>
							<FontAwesomeIcon icon={faBarsStaggered} className="hamb-icon" />
						</Button>
					</div>

					<div className={cls.moduleTitle}>
						<div style={{ width: '100%' }}>{`${(begunModule.processModule.module?.name ?? '').toUpperCase()}${isProcessorOnly ? '*' : ''}`} </div>
						<div style={{ width: '100%' }}>{BYFLOWHERO('10px')}</div>
					</div>

					<div className={cls.userCardContainerTop} >
						{(authenticated && allModules.length > 0 && companyProcesses.length > 0) ?
							<NotificationsProvider>
								<NotificationsMenuEntry />
							</NotificationsProvider>
							: <Spinner type={'BallTriangle'} color="black" height={20} width={`20px`} />}
					</div>

					{/* Mobile */}
					<div className={`mobile-navbar ${nav ? 'open-nav' : ''}`} style={{ width: '100%', backgroundColor: 'transparent' }}>
						<div className={`mobile-navbar ${nav ? 'open-nav' : ''}`} style={{ height: 'inherit', width: '60%', boxShadow: '0px 2px 20px 5px rgba(0, 0, 0, 0.1)', }}>
							<div onClick={openNav} className="mobile-navbar-close">
								<FontAwesomeIcon icon={faRectangleXmark} className="hamb-icon" />
							</div>

							<ul className="mobile-navbar-links">

								<li>
									<a
										style={{ fontFamily: 'Montserrat, serif' }}
										onClick={() => {
											store.dispatch(
												setProcessModule({
													...begunModule.processModule,
													stepIndex: 0,
													isUpdating: false,
												})
											);
											setRefreshComponent(true);
											setShowModuleNavigator(false);
											setTimeout(() => setRefreshComponent(false));
											setNavigateTo({});
											setStart(true);
											setshowTasks(false);
											setShowProfile(false);
											openNav();
										}}
									>
										{'TRANSACTIONS'}
									</a>
								</li>

								<li>
									<a
										style={{ fontFamily: 'Montserrat, serif' }}
										onClick={() => {
											setshowTasks(false);
											setShowProfile(true);
											openNav();
										}}
									>
										{'PROFILE'}
									</a>
								</li>

								{begunModule.processModule?.module?.options?.credits && (
									<li>
										<a
											style={{ fontFamily: 'Montserrat, serif' }}
											onClick={() => {

												getAuthenticatedUser()
													.pipe(first())
													.subscribe({
														next: data => {
															authenticatedUser.options = data?.options;
															authenticatedUser.departments = data?.departments;
															authenticatedUser.positions = data?.positions;
															authenticatedUser.pinned_modules = data?.pinned_modules;
															sessionService?.saveUser?.(authenticatedUser).then(() => {
															});
														},
													});

												setshowTasks(false);
												setShowProfile(false);
												setShowPayments(true);
												openNav();
											}}
										>
											{'WALLET'}
										</a>
									</li>
								)}

								<li>
									<a
										style={{ fontFamily: 'Montserrat, serif' }}
										onClick={() => {
											setUserLogout(true);
											setShowProfile(false);
											openNav();
										}}
									>
										{'LOGOUT'}
									</a>
								</li>
							</ul>

						</div>

					</div>
				</div>
			);
		},
		[nav, loading, taskProcessContext, components]
	);


	const goToTransaction = (selectedProcess, component) => {

		if (!component) {
			return;
		}
		let component_array = [];
		let step_no = 0;

		selectedProcess?.steps?.length > 0 && selectedProcess?.steps?.filter(step => step?.component_blueprints?.filter(cb => cb.selected_fields.filter(field => (field.value = undefined))));
		R.uniqWith(R.eqProps('group'))(component.component_fields).map(f => {
			component_array = [...component_array, { id: f.component_blueprint_id, temp_id: f.id, selected_fields: R.filter(R.propEq('group', f.group))(component.component_fields) }];
		});

		selectedProcess.steps.filter(step => {
			step.component_blueprints.filter(cb => {
				cb.selected_fields.filter(sf => {
					const field_found = component.component_fields.find(cf => cf.field_name === sf.field_name);
					if (sf.data_type_name === 'Status' && status !== undefined) {
						step_no = +step.rank - 1;
					}

					if (field_found != undefined) {
						sf.value = field_found.value;
					} else if (sf.data_type_name === 'Record') {
						const label_found = component.component_fields.find(cf => cf.field_name === sf.options.name);
						if (label_found != undefined) {
							sf.value = label_found.value;
						}
					}
				});
			});
		});

		setTaskProcessContext({
			action: processTaskAction.setComponents,
			value: component_array,
		});
		setTaskProcessContext({
			action: processTaskAction.setModuleItems,
			value: component.component_items,
		});

		store.dispatch(
			setProcessModule({
				...selectedProcess,
				steps: selectedProcess.steps,
				stepIndex: step_no,
				module_id: component.module_id,
				module: allModules.find(mod => mod.id === component.module_id),
				isUpdating: true,
				selectedComponent: component,
			})
		);
		setSelectedComponent(component);

	};

	const moduleComponentsList = module => {
		const array_to_display = R.sortWith([R.descend(R.prop('created_date'))])(components.filter(com => com.module_id === module.id));
		return array_to_display
			.filter(component => (component.component_fields?.filter(f => f.data_type_name === 'Status' && !(f.value === 'Closed' || f.value === 'Cancelled')).length > 0))
			.filter(component => component?.status !== 'Closed')
			.filter(component => !(module.options?.activeFor > 0 && (moment(now()).diff(moment(component.created_date), 'minutes') > +(module.options?.activeFor))) || !(component.status === 'New' || component.status === 'Unassigned') || component.assigned_to?.id || component?.payments?.id)

			.map((component, index) => {
				const status = component.component_fields?.find(f => f.data_type_name === 'Status')?.value;
				const uaction = component.component_fields.find(cf => cf.data_type_name === 'Action Button');
				const paymentT = component.component_fields.find(cf => cf.data_type_name === 'Payments');
				const uactionU = companyUsersArray.find(usr => usr.id === uaction?.value);
				return (
					<div
						key={index}
						className={cls.componentCard}
						style={{ border: selectedComponent?.id === component.id ? '3px dashed black' : '', fontWeight: 'normal' }}
						app-color-variant={`${(index % 5) + 1}`}
						onClick={() => {
							const handler = () => {

								const selectedProcess = companyProcesses.find(proc => proc.id === module.procedure.id);
								setSelectedComponent(component);
								setStart(true);
								companyData.start = true;

								let component_array = [];
								selectedProcess?.steps?.length > 0 && selectedProcess?.steps?.filter(step => step?.component_blueprints?.filter(cb => cb.selected_fields.filter(field => (field.value = undefined))));
								R.uniqWith(R.eqProps('group'))(component.component_fields).map(f => {
									component_array = [...component_array, { id: f.component_blueprint_id, temp_id: f.id, selected_fields: R.filter(R.propEq('group', f.group))(component.component_fields) }];
								});

								selectedProcess.steps.filter(step => {
									step.component_blueprints.filter(cb => {
										cb.selected_fields.filter(sf => {
											const field_found = component.component_fields?.find(cf => cf.field_name === sf.field_name);
											if (field_found != undefined) {
												sf.value = field_found.value;
												sf.group = field_found.group;
											} else if (sf.data_type_name === 'Record') {
												const label_found = component.component_fields?.find(cf => cf.field_name === sf.options.name);
												if (label_found != undefined) {
													sf.value = label_found.value;
													sf.group = label_found.group;
												}
											}
										});
									});
								});

								setTaskProcessContext({
									action: processTaskAction.setComponents,
									value: component_array,
								});
								setTaskProcessContext({
									action: processTaskAction.setModuleItems,
									value: JSON.parse(JSON.stringify(component.component_items)),
								});

								const steps_for_use = selectedProcess.steps
									.filter(step => step?.options?.showStepAll == undefined || step?.options?.showStepAll == true || (step?.options?.showStepAll == false && step?.options?.showOnly?.filter(a => authenticatedUser.positions.map(pos => pos.id).indexOf(a) > -1).length > 0))
									.map((step, i) => {
										return { ...step, rank: i + 1 };
									});

								const authorized =
									begunModule?.processModule?.steps?.length > 0
										? begunModule?.processModule?.steps
											?.find(step => step?.component_blueprints.filter(cb => cb.selected_fields.filter(sf => sf.data_type_name === 'Status').length > 0).length > 0)
											?.component_blueprints?.find(cb => cb.selected_fields.filter(sf => sf.data_type_name === 'Status').length > 0)?.options?.showOnly
										: [];

								const authorized_for_actions =
									begunModule?.processModule?.steps?.length > 0
										? begunModule?.processModule?.steps
											?.find(step => step?.component_blueprints.filter(cb => cb.selected_fields.filter(sf => sf.data_type_name === 'Action Button').length > 0).length > 0)
											?.component_blueprints.find(cb => cb.selected_fields.filter(sf => sf.data_type_name === 'Action Button')?.length > 0)?.options?.showOnly
										: [];

								const allowStatus =
									(status === undefined && (user?.id === selectedProcess?.user_id || authorized?.filter(a => authenticatedUser?.positions?.map(pos => pos.id).indexOf(a) > -1).length > 0)) ||
									((status === undefined || status === 'New') && component?.created_by?.id === authenticatedUser?.id) ||
									(status !== undefined &&
										!(status === 'Closed' || status === companyData?.misc_info?.lock) &&
										authorized?.filter(a => authenticatedUser?.positions?.map(pos => pos.id).indexOf(a) > -1).length > 0 &&
										(allModules?.find(mod => mod.id === component.module_id)?.options?.strict != true || component?.created_by?.id !== user?.id)) ||
									(component?.is_complete === false && component?.created_by?.id === user?.id);

								const allowAction = isUserAuthorized(status, authorized_for_actions, component, user, begunModule, companyData);
								const userAction = component.component_fields.find(cf => cf.data_type_name === 'Action Button')?.value ?? '';

								store.dispatch(
									setProcessModule({
										...selectedProcess,
										completeSteps: selectedProcess.steps,
										steps: steps_for_use,
										stepIndex: (
											allowStatus == true && component.created_by.id === authenticatedUser.id && userAction ? 0 : (allowStatus && (component.created_by.id === authenticatedUser.id || selectedProcess.user_id === authenticatedUser.id)) || (allowAction && (userAction === authenticatedUser.id || selectedProcess.user_id === authenticatedUser.id))
										)
											? 1
											: 0,
										canEdit: (
											allowStatus == true && component.created_by.id === authenticatedUser.id && userAction ? 0 : (allowStatus && (component.created_by.id === authenticatedUser.id || selectedProcess.user_id === authenticatedUser.id)) || (allowAction && (userAction === authenticatedUser.id || selectedProcess.user_id === authenticatedUser.id))
										)
											? true
											: false,
										status: status,
										back_array: steps_for_use.length > 0 ? [steps_for_use[0].id] : [],
										module_id: component.module_id,
										module: allModules?.find(mod => mod.id === component.module_id),
										isUpdating: true,
										selectedComponent: component,
										showDetail: true,
									})
								);

								setRefreshComponent(true);

								if (component?.assigned_to?.id && !companyUsersArray.find(usr => usr.id === component?.assigned_to?.id)) {

									getFilteredUsers(companyUsersArray.find(usr => usr.id === component.created_by?.id) ? [component?.assigned_to?.id] : [component?.assigned_to?.id, component?.created_by?.id])
										.pipe(first())
										.subscribe({
											next: data => {
												store.dispatch(setUsersArray(companyUsersArray.concat(data)));
												setTimeout(() => {
													setRefreshComponent(false);
													window.scrollTo({ top: 0, behavior: 'smooth' });
												});
											},

										});
								} else {
									setTimeout(() => {
										setRefreshComponent(false);
										window.scrollTo({ top: 0, behavior: 'smooth' });
									});
								}


							};

							handler();
						}}
					>
						{component.title.length > 24 ? <h5>{component.title}</h5> : <h4>{component.title}</h4>}
						<div style={{ fontSize: '16px', marginBottom: '5px' }}>{component.status === 'Unassigned' ? component.status : uaction?.options?.action === 'status' && uaction?.value ? uaction?.value : status ? (status === 'New' && uaction ? 'New (Assigned)' : `${status}`) : 'No Status'}</div>
						<div style={{ fontSize: '12px' }}>
							{status === 'New'
								? `${paymentT ? paymentT?.value : ''}`
								: component.created_by.id !== authenticatedUser.id
									? `${component.created_by.first_name} ${component.created_by.last_name}`
									: uactionU
										? `${uactionU.first_name} ${uactionU.last_name}`
										: `${paymentT ? paymentT?.value : ''}`}
						</div>
						{moment(component.created_date).format('DD MMM YYYY')}
					</div>
				);
			});
	};

	const dashboardList = () => {
		let fields_to_display = [];

		allModules.filter(mod => mod.id === begunModule?.processModule?.module?.id).filter(mod =>
			companyProcesses
				.filter(proc => mod.procedure.id === proc.id && proc.steps.length > 0 && proc?.steps?.filter(step => step.component_blueprints.filter(cb => cb.options?.asDashboard == true)))
				.filter(proc =>
					proc.steps
						.filter(st => st.component_blueprints.filter(cb => cb.options?.asDashboard == true))
						.filter(s =>
							s.component_blueprints
								.filter(c => c.options?.asDashboard == true)
								.filter(cbp =>
									cbp.selected_fields
										.filter(sf => sf.visible == true && sf.data_type_name === 'Formula')
										.map(sf => {
											if (user?.positions?.filter(pos => cbp.options?.showOnly?.includes(pos.id)).length > 0 || cbp.options?.showOnly?.length == 0 || cbp.options?.showOnly === undefined) {
												fields_to_display = [...fields_to_display, { ...sf, module_id: mod.id, procedure: proc, module: mod }];
											} else {
												fields_to_display = [...fields_to_display];
											}
										})
								)
						)
				)
		);

		if (fields_to_display.length > 0) {
			if (isMobile == true) {
				fields_to_display = fields_to_display.slice(0, 2);
			}

			const dashboard_array = fields_to_display.map((f, i) => {
				let value = f.value ?? 0;
				if (f.data_type_name === 'Formula') {
					value = computeFormula(f.options, components, [], [], []);
				}
				return (
					<div
						key={i}
						className={cls.dashboardCard}
						style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}
						app-color-variant={`${(i % 5) + 1}`}
						onClick={() => {
							store.dispatch(setShowMenu(false));
							store.dispatch(
								setProcessModule({
									...f.procedure,
									stepIndex: 0,
									module_id: f.module_id,
									module: f.module,
								})
							);

							if (f?.options?.operator === 'countdateocc') {
								setNavigateTo({ module_id: f.module_id, key: f.options.list.length > 0 ? f.options.list[0] : 'Status', value: moment(getDateForm(f?.options?.searchtext)).format('DD MMM YYYY') });
							} else {
								setNavigateTo({ module_id: f.module_id, key: f.options.list.length > 0 ? f.options.list[0] : 'Status', value: f.options.searchtext ?? '' });
							}

							store.dispatch(
								setProcessModule({
									...begunModule.processModule,
									stepIndex: 0,

									isUpdating: false,
								})
							);
							setStart(true);
							setRefreshComponent(true);
							setshowTasks(false);
							setShowProfile(false);
							setTimeout(() => setRefreshComponent(false));
						}}
					>
						<div style={{ fontSize: '12px' }}>{`Dashboard`}</div>
						<h3>{f.field_name}</h3>

						{value}
					</div>
				);
			});

			return dashboard_array ?? [];
		}
	};

	const graphicalComponentsList = () => {
		const module = allModules.find(mod => mod?.options?.asGraphical == true);

		if (module === undefined) {
			return [];
		}

		const array_to_display = R.sortWith([R.descend(R.prop('created_date'))])(components.filter(com => com.module_id === module.id));

		return array_to_display
			.filter(component => component.component_fields?.filter(f => f.data_type_name === 'Status' && !(f.value === companyData.misc_info?.lock || f.value === 'Closed' || f.value === 'Cancelled')).length > 0)
			.map((component, index) => {
				const imageC = component.component_fields.find(cf => cf.data_type_name === 'Link');
				return (
					<div key={index} className={cls.imageCard}>
						{imageC && (`${imageC?.value}`?.includes('jpg') || `${imageC?.value}`?.includes('jpeg') || `${imageC?.value}`?.includes('png') || imageC?.options?.image) && <img alt="lg" src={imageC?.value} style={{ objectFit: 'contain', height: '160px', borderRadius: '5px' }} />}
					</div>
				);
			});
	};
	const renderCarousell = module => {
		const dashboardArray = allModules.length > 0 ? dashboardList() : [];
		const componentsArray = module ? moduleComponentsList(module) : [];
		const graphicalsArray = allModules.length > 0 ? graphicalComponentsList() : [];

		return (
			<div className={cls.center} style={{ height: (graphicalsArray?.length > 0 || dashboardArray?.length > 0 || componentsArray?.length > 0) ? '' : '0px', width: '90%', minWidth: '360px', backgroundColor: 'var(--app-color-gray-lightest)', flexDirection: 'column' }}>
				<Swiper
					slidesPerView={'auto'}
					centeredSlides={true}
					spaceBetween={10}
					initialSlide={1}
					pagination={{
						clickable: true,
						dynamicBullets: true,
					}}
					modules={[Autoplay, Pagination]}
					className="mySwiper"
				>
					{graphicalsArray?.length > 0 &&
						graphicalsArray.map((page, index) => {
							return (
								<SwiperSlide key={index} className={cls.swiperslide}>
									{page}
								</SwiperSlide>
							);
						})}
					{dashboardArray?.length > 0 &&
						dashboardArray.map((page, index) => {
							return (
								<SwiperSlide key={index} className={cls.swiperslide}>
									{page}
								</SwiperSlide>
							);
						})}
					{componentsArray?.length > 0 &&
						componentsArray.map((page, index) => {
							return (
								<SwiperSlide key={index} className={cls.swiperslide}>
									{page}
								</SwiperSlide>
							);
						})}
				</Swiper>
			</div>
		);
	};

	const moduleNavigator = () => {
		const mods_to_display = allModules
			.filter(mod => mod?.options?.asMobileApp === true);
		return (
			<div className={cls.center} style={{ textAlign: 'left', height: mods_to_display.length > 4 ? `${2 * 26}vh` : '20vh', padding: '0px 10px 0px 20px ', width: '100%', flexDirection: 'column', marginTop: '10px' }}>
				<Swiper
					slidesPerView={mods_to_display.length > 4 ? 4 : mods_to_display.length}
					grid={{
						rows: mods_to_display.length > 4 ? 2 : 1,
					}}
					spaceBetween={1}
					pagination={{
						clickable: true,
					}}
					modules={[Grid, Pagination]}
					style={{ display: 'flex', flexDirection: 'column' }}
				>
					{mods_to_display
						.map((module, index) => {
							return (
								<SwiperSlide key={index}
								>
									<Button
										clear
										clicked={() => {
											const ipo = user?.positions?.filter(pos => module?.options?.processors?.indexOf(pos.id) > -1)?.length > 0
											setIsProcessorOnly(ipo);
											setInitialScreen(allModules, module, ipo);
											setShowModuleNavigator(true);
											setShowProfile(false);
											setshowTasks(false);
										}}
										style={{ fontSize: '12px', fontFamily: 'var(--app-text-mobile-font)', width: '100%', height: '16vh', flexDirection: 'column' }}
									>
										<img alt="lg" src={module.options?.background} style={{ objectFit: 'contain', width: '10vh', height: '13vh', borderRadius: '5px' }} />
										{module.name}
									</Button>
								</SwiperSlide>
							);
						})}
				</Swiper>
			</div>
		);
	};

	const DashboardMobileTasks = memo(() => {

		return <div style={{ padding: '0 5px' }}>

			<div style={{ display: 'flex', textAlign: 'left' }}>
				<p className={cls.stepTitle}>{'My Tasks'}</p>
				<Button
					light
					clicked={() => {
						setLoading(true);
						getAllTasks(user.id, true)
							.pipe(take(1))
							.subscribe({
								complete: () => setLoading(false)
							})
					}}
					icon={<img src={reset2} />}

				/>
				<Button
					light
					style={{ display: user?.ui_permissions?.find(permis => permis === 'view-dashboard-task') ? '' : 'none' }}
					clicked={() => {
						setShowTask(true);
					}}
					icon={<img src={add} />}
				/>
			</div>
			<ModuleLists
				title={''}
				card={true}
				className={cls.listContainer}
				isReport={false}
				app-variant-is-mobile={'true'}
				columns={['Name', 'Status', 'Due']}
				list={allTasks.filter(task => task.status !== 'Done').map(task => {
					return {
						Name: task.name,
						Description: task.description,
						Status: task.status ?? 'New',
						Due: moment(task.add_info?.end).format('DD MMMM h:mm a'),
						['Created By']: `${task.creator?.first_name ?? ''} ${task.creator?.last_name ?? ''}`,
						['Assigned To']: `${task.assignee?.first_name ?? ''} ${task.assignee?.last_name ?? ''}`,
						task: task
					}
				})}
				onListItemClick={item => {
					setShowTask(item.task);

				}}
			>
			</ModuleLists>
		</div >
	});
	return (
		<div className={cls.center} style={{ flexDirection: 'column', alignItems: 'center' }}>
			{begunModule?.processModule?.stepIndex == undefined || begunModule?.processModule?.stepIndex < 2 ? allModules.length > 0 &&
				<Navbar /> : <div />}
			{(begunModule?.processModule?.stepIndex !== 0 && allModules?.length === 0) &&
				<Navbar />}
			{(begunModule?.processModule?.stepIndex !== 0 && allModules?.length === 0) &&
				<div style={{ display: 'flex', margin: 'auto', width: '100%', height: '500px', alignContent: 'center', alignItems: 'center', justifyItems: 'center', justifyContent: 'center' }}>{FLOWHERO('30px')}</div>}
			{(begunModule?.processModule?.stepIndex == undefined || begunModule?.processModule?.stepIndex < 2) ? allModules.length > 0 && !showProfile && !showTasks &&
				renderCarousell(begunModule?.processModule?.module ?? allModules?.find(mod => mod?.options?.asMobileApp === true)) : <br />}
			{(begunModule?.processModule?.stepIndex < 2 && showModuleNavigator && !showProfile) ?
				moduleNavigator(begunModule?.processModule?.module ?? allModules?.find(mod => mod?.options?.asMobileApp === true)) : <br />}
			{!(showProfile || showTasks) &&
				<div className={cls.host} style={{ display: companyData.status === 'inactive' ? 'none' : '', width: '100%' }}>
					<div className={cls.dashboardPanel} style={{ display: 'flex', margin: '0px 0 0 0' }}>
						{(begunModule?.processModule?.steps?.length > 0 && blueprints.length > 0 && !refreshComponent) ? (
							<ProcessModule
								isProcessorOnly={isProcessorOnly}
								paymentID={paymentID}
								navigateTo={navigateTo}
								selectedComponent={selectedComponent ?? {}}
								setSelectedComponent={setSelectedComponent}
								selectedProcess={begunModule.processModule}
								onCancel={() => {
									setStart(false);
									setRefreshComponent(true);
									setTimeout(() => setRefreshComponent(false));
								}}
							/>
						) :
							<Spinner type={'BallTriangle'} color="black" height={40} width={`40px`} />
						}

					</div>
				</div>}
			{showProfile &&
				<div style={{ width: '100%', padding: '0 40px' }}>
					<MyProfile />
				</div>}
			{showTasks &&
				<div style={{ width: '100%', padding: '0 10px' }}>
					<DashboardMobileTasks />
				</div>}
			{(begunModule?.processModule?.stepIndex == undefined || begunModule?.processModule?.stepIndex < 2) && allModules.length > 0 &&
				<div style={{ display: 'flex', position: 'fixed', bottom: '0', width: '100%' }}>
					<Tabbar />
				</div>
			}
			{showPayments && (
				<ModalR
					hasCloseIcon
					isOpen={showPayments}
					onClose={() => {
						setShowPayments(false);
						setAmount(-1);
					}}
					backdropColor={true}
					blured={false}
					scrollable={true}
					shadowedContainerStyle={{ width: '100%' }}
				>
					<div className={cls.userPanelRows} style={{ height: 'auto', minHeight: '540px', marginTop: '20px', display: amount > 0 ? 'none' : '' }}>
						<LabeledContainer label={'Billing & Payments'} />
						<div className={cls.wallet} >
							<div style={{ color: 'white', fontSize: '14px', }}>{`Reward Points:`}</div>
							<div style={{ color: 'white', fontSize: '20px', fontWeight: 'bold', margin: '5px 0 20px' }}>{user?.options?.points?.toFixed(2) ?? '0.00'} Pts</div>
							<div style={{ color: 'white', fontSize: '14px', }}>{`Payments Wallet:`}</div>
							<div style={{ color: 'white', fontSize: '20px', fontWeight: 'bold', margin: '5px 0 80px' }}>Php {authenticatedUser?.options?.credit?.toFixed(2) ?? '0.00'}</div>
							<div className={cls.actionsContainer}>
								<Button clear title={'Top Up'} clicked={() => setAmount(0)} style={{ backgroundColor: 'var(--app-color-blue)', color: 'white', width: '140px', display: amount > -1 ? 'none' : '' }} />
							</div>
							<div style={{ width: 'inherit', display: amount === -1 ? 'none' : '' }}>
								<div style={{ color: 'white' }} onClick={() => setAmount(-1)}>{'Select your denomination:'}</div>
								<Button light title={'Php 500.00'} clicked={() => setAmount(500)} style={{ width: '100%' }} />
								<Button light title={'Php 1000.00'} clicked={() => setAmount(1000)} style={{ width: '100%' }} />
								<Button light title={'Php 2000.00'} clicked={() => setAmount(2000)} style={{ width: '100%' }} />
								<Button light title={'Php 3000.00'} clicked={() => setAmount(3000)} style={{ width: '100%' }} />
							</div>
						</div>
						<div style={{ display: 'flex', justifyContent: 'space-between' }}>
							<Button
								loading={loading}
								light
								title={showHistory ? 'Hide' : 'Show Recent Payments'}
								style={{ marginRight: '40px' }}
								clicked={() => {
									if (showHistory) {
										setShowHistory(false)
									} else {
										setLoading(true);
										getPurchases()
											.pipe(first())
											.subscribe({
												next: data => {
													setLoading(false);
													setShowHistory(true);
													setInvoices(data.data);
												},
												error: () => { },
											});
									}

								}}
							/>
						</div>
						<div style={{ display: showHistory ? '' : 'none', padding: '20px 0px' }}>
							{'Recent Payments:'}
							<br />
							<br />
							<ModuleLists
								title={''}
								card={true}
								className={cls.listContainer}
								isReport={false}
								app-variant-is-mobile={'true'}
								columns={['Date', 'ID', 'Source', 'Amount']}
								list={invoices.map(inv => {
									return {
										Date: `${moment(inv.starts_at).format('YY MMM DD h:mm a')}`,
										ID: `${inv.stripe_product}`,
										Source: `${inv.stripe_status}`,
										Amount: `${(+inv.stripe_price)?.toFixed(2)}`
									}
								})}
							>
							</ModuleLists>
						</div>
					</div>

					{amount > 0 ? (
						<CheckoutPage
							label={' '}
							withBack={false}
							paymentPath={'wallet'}
							onClose={() => setShowPayments(false)}
							companyData={companyData}
							subscriptionInfo={{
								user_id: authenticatedUser.id,
								ref: authenticatedUser.id,
								plan_price: amount,
								currency: 'PHP',
								plan: `Credits`,
								total: amount,
								billable_users: [user],
								duration: 0,
								dlabel: 'Flowhero',
								id: generateNewUUID(),
							}}
							onError={() => setShowPayments(false)
							}
						/>
					) : (
						<>
							<p style={{ fontSize: '10px' }}>{PAYMONGO_KEY !== '' ? `Payments by` : ``}</p>
							<img src={'https://files.readme.io/7586db8-small-paymongo_api_white.png'} style={{ backgroundColor: 'black', height: '30px', width: '140px' }} />
						</>
					)}
				</ModalR>
			)}
			{showTask &&
				<MobileTaskModal
					modalVisible={showTask}
					heading={`Task for ${begunModule?.processModule?.module?.name}`}
					task={showTask === true ? {} : showTask}
					isProcess={true}
					blured={true}
					shadowedContainerStyle={{ width: '100%' }}
					onClose={() => setShowTask(false)}
					isEdit={showTask === true ? false : true}
					labels={{ valueL: 'Value' }}
				/>
			}
			{userLogout && (
				<LogoutModal openModal={userLogout} setOpenModal={setUserLogout} slug={companyData?.slug} />
			)}
		</div>
	);
};
const mapStateToProps = store => {
	const { begunModule, allTasks, allCompletedTasks, allCompletedTasksPaginated, companyProcesses, allModules, components, companyData, companyUsersArray, blueprints, payable, lastUpdates } = store.flowReducer;
	const { user, authenticated } = store.sessionReducer;
	const { authenticatedUser } = store.authReducer;

	return { begunModule, allTasks, allCompletedTasks, allCompletedTasksPaginated, companyProcesses, allModules, components, companyData, companyUsersArray, user, authenticatedUser, authenticated, blueprints, payable, lastUpdates };
};

const mapDispatchToProps = dispatch => {
	return {
		onSetSelectedProcess: process => dispatch(setSelectedProcess(process)),
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(DashboardMobile);
